@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro');

html, body {
	margin: 0;
	font-family:"Source Sans Pro", sans-serif;
	-webkit-font-smoothing: antialiased;
	height: 100%;

	@media (min-width: 640px) {
		overflow: hidden;
		width: 100%;
	}

body.template {
	@import "customer.less";
}

}
* {
	box-sizing: border-box;
}

input, select {
	font-family: "Source Sans Pro",sans-serif;
}

#background {
	background-position: 50% 50%;
}

#zoom-out {
	position: fixed;
	top: 40px;
	left: 50px;
	display: none;
	width: 40px;
	height: 40px;
	border: 1px solid white;
	background: rgba(0,0,0,0.5);
	border-radius: 50%;
	cursor: pointer;
	text-align: center;
	svg {
		margin-top: 18%;
		width: 60%;
		height: 60%;
		path {
			fill: white;
		}
	}
	&:hover {
		background: rgba(0,0,0,0.8);
	}
}

#visualisation-container {
	position: relative;
}

#controls {
	padding: 15px; // Must match the controls width calculation in updateColumnFilter
	position: relative;
	box-shadow: 0 0 5px rgba(0,0,0,0.25);
	border-radius: 3px;
	transform-origin: bottom right;

	// When controls are hidden shrink down to the cog
	transform: scale(0);
	margin: 0;
	bottom: 5px;
	right: 5px;
	display: none;

	// When they are showing, animate or snap them into view.
	// Animate when the container is fixed, but not if we're
	// calling setHeight, since we don't want to animate the
	// window height, triggering resize and update.
	body.controls-showing & {
		transform: scale(1);
		margin: 10px;
		bottom: 0;
		right: 0;
	}
	&.wide-view, &.fixed-height {
		transition: transform 0s ease, margin 0s ease, bottom 0s ease, right, 0s ease;
		position: absolute;
	}

	&.narrow-view {
		width: ~"calc(100% - 20px)";
		height: 0;
		body.controls-showing & {
			height: auto;
		}
		&.fixed-height {
			height: auto;
		}
		@media (min-width: 400px) {
			form {
				column-count: 2;
			}
		}
	}

	.controls-toggle.close {
		position: absolute;
		width: 14px;
		height: 14px;
		top: 8px;
		right: 8px;
		font-weight: bold;
		cursor: pointer;
		svg {
			path {
				stroke-linecap: round;
				stroke-width: 8;
			}
		}
		&:hover {
			opacity: 0.8;
		}
	}

	#mode-menu {
		color: #ffffff;
		padding: 0 2px 1em;
		text-transform: uppercase;
		font-size: 1em;
		font-weight: bold;
		&.dark {
			.mode {
				&.groups {
					margin-right: 7px;
					background-image: url("bubble_icon_dark.svg");
				}
				&.map {
					margin-right: 7px;
					background-image: url("map_icon_dark.svg");
				}
				&.histogram	 {
					background-image: url("histogram_icon_dark.svg");
				}
				&.scatter	 {
					background-image: url("scatter_icon_dark.svg");
				}
			}
		}
		&.light {
			.mode {
				&.groups {
					margin-right: 7px;
					background-image: url("bubble_icon_light.svg");
				}
				&.map {
					margin-right: 7px;
					background-image: url("map_icon_light.svg");
				}
				&.histogram	 {
					background-image: url("histogram_icon_light.svg");
				}
				&.scatter	 {
					background-image: url("scatter_icon_light.svg");
				}
			}
		}
		.mode {
			opacity: 0.5;
			cursor: pointer;
			width: 3em;
			height: 2em;
			display: inline-block;
			border: 1px;
			background-size: 100%;
			background-repeat: no-repeat;
			&.groups {
				margin-right: 7px;
			}
			&.map {
				margin-right: 7px;
			}
			&:hover {
				opacity: 0.8;
			}
			&.selected, &.selected:hover {
				opacity: 1;
				cursor: default;
			}
		}
	}

	#filter {
		width: 100%;
	}

	select {
		width: 100%;
		font-size: 1em;
		background: #fff;
		border: 1px solid #E3E3E3;
		color: #333;
	}

	.select-container {
		margin-bottom: 0.5em;
		width: 100%;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.select-title {
		margin: 0 0 0.25em;
		font-size: 1em;
	}
}

#controls h2.select-title {
	font-weight: 600;
}

#legend {
	.legend-head, .legend-blocks {
		display: inline;
		vertical-align: middle;
	}
	.legend-head {
		margin-right: 0.5em;
		&:empty {
			margin: 0;
		}
	}
	.series {
		margin-right: 6px;
		display: inline-block;
		&:last-child {
			margin-right: 0;
		}
	}
	.swatch {
		margin-bottom: 1px;
		width: 15px;
		height: 15px;
		border-radius: 50%;
		display: inline-block;
		margin-right: 3px;
		vertical-align: middle;
		@media screen and (max-width: 520px) {
			height: 10px;
			width: 10px;
		}
	}
	.label {
		display: inline-block;
		font-size: 13px;
		line-height: 1em;
		vertical-align: middle;
		@media screen and (max-width: 520px) {
			font-size: 11px;
		}
	}
}

#main {
	position: relative;
	width: 100%;

	.content {
		position: absolute;
		margin: 0;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}

	#labels {
		text, tspan {
			font-weight: 300;
			font-weight: bold;
		}
	}
}

.controls-toggle.cog {
	position: absolute;
	bottom: 0px;
	right: 0px;
	cursor: pointer;
	&:hover {
		opacity: 0.85;
	}
}

.flourish-popup {
	pointer-events: none;
}
